import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { AxiosError } from 'axios';
import http from '../../utils/api/index';
import { API } from '../../utils/api/api';

export enum CURRENCY {
  AUD = 'AUD',
  USD = 'USD',
  EUR = 'EUR',
  CNY = 'CNY',
}

export enum CRYPTO {
  BTC = 'BTC',
  ETH = 'ETH',
  BNB = 'BNB',
  USDT = 'USDT',
}

type UserType = {
  id: number;
  username: string;
};

type UserDataType = {
  username: string;
  password: string;
};

type RateDataType = {
  currency: string;
  cryptocurrency: string;
};

type AuthStateType = {
  user: UserType | null;
  authenticated: boolean;
};

const initialState: AuthStateType = {
  user: null,
  authenticated: false,
};

export const register = createAsyncThunk(
  'register',
  async (data: UserDataType, thunkApi) => {
    try {
      const res = await http.post(API.register, {
        username: data.username,
        password: data.password,
      });
      return JSON.stringify(res);
    } catch (error) {
      return thunkApi.rejectWithValue(
        JSON.stringify((error as AxiosError).response)
      );
    }
  }
);

export const login = createAsyncThunk(
  'login',
  async (data: UserDataType, thunkApi) => {
    try {
      const res = await http.post(API.login, {
        username: data.username,
        password: data.password,
      });
      return JSON.stringify(res);
    } catch (error) {
      return thunkApi.rejectWithValue(
        JSON.stringify((error as AxiosError).response)
      );
    }
  }
);

export const profile = createAsyncThunk('profile', async (_, thunkApi) => {
  try {
    const res = await http.get(API.profile);
    return JSON.stringify(res);
  } catch (error) {
    return thunkApi.rejectWithValue(
      JSON.stringify((error as AxiosError).response)
    );
  }
});

export const getRate = createAsyncThunk(
  'rate',
  async (data: RateDataType, thunkApi) => {
    try {
      const res = await http.get(
        API.rate + data.currency + '&' + data.cryptocurrency
      );
      return JSON.stringify(res);
    } catch (error) {
      return thunkApi.rejectWithValue(
        JSON.stringify((error as AxiosError).response)
      );
    }
  }
);

export const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    logout: (state) => {
      state.user = null;
      state.authenticated = false;
      localStorage.removeItem('token');
    },
  },
  extraReducers: {
    // login states
    [login.pending.type]: () => {},
    [login.fulfilled.type]: (state, action: PayloadAction<string>) => {
      const { data } = JSON.parse(action.payload as string);
      localStorage.setItem('token', data.access_token);
      state.authenticated = true;
    },
    [login.rejected.type]: () => {},
    // profile states
    [profile.pending.type]: () => {},
    [profile.fulfilled.type]: (state, action: PayloadAction<string>) => {
      const { data } = JSON.parse(action.payload);
      const user: UserType = {
        id: data.id,
        username: data.username,
      };
      state.authenticated = true;
      state.user = user;
    },
    [profile.rejected.type]: (state) => {
      state.authenticated = false;
      state.user = null;
    },
  },
});

export const { logout } = authSlice.actions;

export default authSlice.reducer;
